.footer {
   position: relative;
   padding: 32px 0 20px;
   @include xs {
      padding: 25px 0 20px; }
   &::after {
      position: absolute;
      top: 100%;
      left: 0;
      width: 100%;
      height: 3px;
      background-image: linear-gradient(to right, #1799ed 0%, #59bffa 100%);
      content: ''; }
   &__container {
      display: flex;
      align-items: center;
      justify-content: space-between;
      margin-bottom: 30px;
      @include xs {
         flex-direction: column;
         margin-bottom: 20px; } }
   &__title {
      flex: 0 1 240px;
      @include xs {
         flex: 0 1 auto;
         text-align: center; }
      span {
         color: $color1;
         @include f('ms900'); } }
   &__copyright {
      padding: 24px 0 0;
      font-size: 15px;
      color: #a9b0b3;
      text-align: center;
      border-top: 1px solid rgba(#469dd6, .2); } }
