         height: 300px; }
      .gmnoprint, .gm-style-cc {
         display: none; } }
   &__container {
      padding-top: 80px;
      @include xs {
         padding: 0; } }
   &__info {
      position: relative;
      width: 360px;
      padding: 45px 28px 60px;
      box-shadow: 0 16px 15px rgba(6, 44, 69, 0.13);
      border-radius: 3px 3px 17px 17px;
      background: #fff;
      @include xs {
         width: 100%;
         padding: 30px 15px;
         border-radius: 0; }
      &::before {
         position: absolute;
         top: 0;
         left: 0;
         width: 100%;
         height: 4px;
         border-radius: 2px 2px 0 0;
         background-image: linear-gradient(to right, #1799ed 0%, #59bffa 100%);
         content: '';
         @include xs {
            border-radius: 0; } }
      &-title {
         margin-bottom: 10px;
         font-size: 36px;
         line-height: 40px; }
      &-inner {
         margin-bottom: 30px; }
      &-address {
         position: relative;
         padding-left: 35px;
         margin-bottom: 15px;
         font-style: normal; }
      &-tel {
         @include f('ms900');
         font-size: 20px;
         line-height: 20px;
         color: rgba(#5a5f62, .9);
         transition: .3s ease-in-out;
         &:hover {
            color: #1799ed; } }
      &-btn {}
      &-icon {
         margin-right: 17px;
         color: $color1;
         &.zmdi-pin-drop {
            position: absolute;
            top: 2px;
            left: 5px;
            margin-right: 0; } } } }
