$icons: (0:0);
$icons: map-merge($icons,('calendar': (X: 0px, Y:-80px, W: 80px, H: 80px, TW: 338px, TH: 325px, IMG: '../img/sprite.png')));
$icons: map-merge($icons,('checklist': (X: 0px, Y:-260px, W: 47px, H: 65px, TW: 338px, TH: 325px, IMG: '../img/sprite.png')));
$icons: map-merge($icons,('coin-hand': (X: -286px, Y:-155px, W: 48px, H: 65px, TW: 338px, TH: 325px, IMG: '../img/sprite.png')));
$icons: map-merge($icons,('docs': (X: -196px, Y:0px, W: 80px, H: 80px, TW: 338px, TH: 325px, IMG: '../img/sprite.png')));
$icons: map-merge($icons,('handshake': (X: 0px, Y:0px, W: 96px, H: 70px, TW: 338px, TH: 325px, IMG: '../img/sprite.png')));
$icons: map-merge($icons,('magnifying-glass': (X: -196px, Y:-90px, W: 70px, H: 70px, TW: 338px, TH: 325px, IMG: '../img/sprite.png')));
$icons: map-merge($icons,('med-center': (X: -160px, Y:-170px, W: 65px, H: 65px, TW: 338px, TH: 325px, IMG: '../img/sprite.png')));
$icons: map-merge($icons,('med-insurance': (X: -286px, Y:-80px, W: 51px, H: 65px, TW: 338px, TH: 325px, IMG: '../img/sprite.png')));
$icons: map-merge($icons,('money-shield': (X: -90px, Y:-170px, W: 60px, H: 80px, TW: 338px, TH: 325px, IMG: '../img/sprite.png')));
$icons: map-merge($icons,('peoples': (X: -106px, Y:0px, W: 80px, H: 80px, TW: 338px, TH: 325px, IMG: '../img/sprite.png')));
$icons: map-merge($icons,('rocket': (X: -106px, Y:-90px, W: 65px, H: 65px, TW: 338px, TH: 325px, IMG: '../img/sprite.png')));
$icons: map-merge($icons,('smartphone': (X: -286px, Y:0px, W: 52px, H: 70px, TW: 338px, TH: 325px, IMG: '../img/sprite.png')));
$icons: map-merge($icons,('zoom-docs': (X: 0px, Y:-170px, W: 80px, H: 80px, TW: 338px, TH: 325px, IMG: '../img/sprite.png')));


// Gets an attribute from the sass map
@function icon-attr($icon, $attr) {
  $icon: map-get($icons, $icon);
  @return map-get($icon, $attr); }

@mixin sprite($iconName) {
  background-image: url(icon-attr($iconName, IMG));
  width: icon-attr($iconName, W);
  height: icon-attr($iconName, H);
  background-position: icon-attr($iconName, X) icon-attr($iconName, Y); }
@mixin sprite-position($iconName) {
  background-position: icon-attr($iconName, X) icon-attr($iconName, Y); }
@mixin sprite-retina($iconName) {
  background-image: url(icon-attr($iconName, IMG));
  $width: icon-attr($iconName, W);
  $height: icon-attr($iconName, H);
  width: $width/2;
  height: $height/2;
  $x: icon-attr($iconName, X);
  $y: icon-attr($iconName, Y);
  background-position: $x/2 $y/2;
  $tw: icon-attr($iconName, TW);
  $th: icon-attr($iconName, TH);
  background-size: $tw/2 $th/2; }


@mixin s($i) {
  @include sprite($i); }
@mixin sp($i) {
  @include sprite-position($i); }
@mixin sr($i) {
  @include sprite-retina($i); }


